.category-btn {
  display: block;
  padding: 2rem;
  text-align: center;
  background: linear-gradient(135deg, #2c3e50 0%, #1a252f 100%);
  color: white;
  text-decoration: none;
  border-radius: 15px;
  font-size: 1.5rem;
  font-weight: 600;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: all 0.3s ease;
  box-shadow: 0 10px 20px rgba(0,0,0,0.1);
  position: relative;
  overflow: hidden;
}

.category-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, #3498db, #2ecc71);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 1;
}

.category-btn:hover::before {
  opacity: 0.2;
}

.category-btn:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px rgba(0,0,0,0.2);
}

.category-btn span {
  position: relative;
  z-index: 2;
}

.container {
  padding: 2rem;
}

h1 {
  color: #2c3e50;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: 3rem;
}

@media (max-width: 768px) {
  .category-btn {
    padding: 1.5rem;
    font-size: 1.2rem;
  }
} 