.cart-page {
  padding: 2rem 0;
}

.cart-item {
  background: white;
  border-radius: 10px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.cart-item:hover {
  transform: translateY(-5px);
}

.cart-item img {
  border-radius: 8px;
  object-fit: cover;
}

.cart-item p {
  margin: 0.5rem 0;
  color: #2c3e50;
}

.cart-summary {
  background: white;
  border-radius: 10px;
  padding: 1.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.cart-summary h4 {
  color: #2c3e50;
  margin-bottom: 1rem;
}

.cart-summary hr {
  margin: 1rem 0;
  border-color: rgba(0, 0, 0, 0.1);
}

.payment-methods {
  margin: 1.5rem 0;
}

.payment-methods h5 {
  color: #2c3e50;
  margin-bottom: 1rem;
}

.address-section {
  margin-top: 1.5rem;
  padding-top: 1.5rem;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.address-section h4 {
  color: #2c3e50;
  margin-bottom: 0.5rem;
}

.address-section h5 {
  color: #34495e;
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.qr-modal {
  text-align: center;
  padding: 1rem;
}

.qr-modal p {
  color: #2c3e50;
  margin: 0.5rem 0;
}

@media (max-width: 768px) {
  .cart-page {
    padding: 1rem;
  }
  
  .cart-item {
    padding: 1rem;
  }
  
  .cart-summary {
    margin-top: 1.5rem;
  }
}