.category {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2.5rem;
  margin-top: 2rem;
  padding: 2rem;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
}

.category .card {
  width: 22rem;
  border: none;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0,0,0,0.1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  background: #363535;
  position: relative;
}

.category .card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #3498db, #dfac20);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.category .card:hover::before {
  opacity: 1;
}

.category .card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 30px rgba(0,0,0,0.15);
}

.category .card img {
  height: 250px;
  width: 100%;
  object-fit: contain;
  padding: 1.5rem;
  background: linear-gradient(135deg, #f8f9fa 0%, #505255 100%);
  transition: all 0.3s ease;
}

.category .card:hover img {
  transform: scale(1.05);
  padding: 1rem;
}

.card-name-price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  background: #fff;
  border-top: 1px solid rgba(0,0,0,0.05);
}

.card-name-price h5 {
  margin: 0;
  font-weight: 600;
  color: #2c3e50;
  font-size: 1.2rem;
  letter-spacing: 0.5px;
}

.card-price {
  color: #e74c3c;
  font-weight: 700;
  font-size: 1.3rem;
  background: linear-gradient(45deg, #e74c3c, #c0392b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.category .card-body {
  padding: 1.5rem;
  background: #fff;
}

.category .card-text {
  color: #34495e;
  margin-bottom: 1.5rem;
  font-size: 1rem;
  line-height: 1.6;
}

.category .btn {
  padding: 0.7rem 1.2rem;
  border-radius: 8px;
  font-weight: 600;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-size: 0.9rem;
  flex: 1;
  margin: 0 0.3rem;
  min-width: 140px;
}

.category .btn-primary {
  background: linear-gradient(45deg, #3498db, #2980b9);
  border: none;
  color: white;
  box-shadow: 0 4px 15px rgba(52, 152, 219, 0.3);
}

.category .btn-primary:hover {
  background: linear-gradient(45deg, #2980b9, #3498db);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(52, 152, 219, 0.4);
}

.category .btn-warning {
  background: linear-gradient(45deg, #2ecc71, #27ae60);
  border: none;
  color: white;
  box-shadow: 0 4px 15px rgba(46, 204, 113, 0.3);
}

.category .btn-warning:hover {
  background: linear-gradient(45deg, #27ae60, #2ecc71);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(46, 204, 113, 0.4);
}

/* Filter section styles */
.filter-section {
  background: linear-gradient(135deg, #2c3e50 0%, #1a252f 100%);
  padding: 1rem;
  border-radius: 10px;
  color: white;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  margin-bottom: 1rem;
}

.filter-section h4 {
  color: #ecf0f1;
  border-bottom: 2px solid #3498db;
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;
  font-weight: 600;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  font-size: 1rem;
}

.filter-section .ant-checkbox-wrapper {
  color: #ecf0f1;
  margin-bottom: 0.5rem;
  transition: all 0.3s ease;
  font-size: 0.9rem;
}

.filter-section .ant-radio-wrapper {
  color: #ecf0f1;
  margin-bottom: 0.5rem;
  transition: all 0.3s ease;
  font-size: 0.9rem;
}

.filter-section .btn-danger {
  background: linear-gradient(45deg, #e74c3c, #c0392b);
  border: none;
  padding: 0.6rem 1rem;
  border-radius: 5px;
  font-weight: 600;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  font-size: 0.8rem;
  box-shadow: 0 4px 15px rgba(231, 76, 60, 0.3);
  transition: all 0.3s ease;
  width: 100%;
  margin-top: 1rem;
}

.filter-section .btn-danger:hover {
  background: linear-gradient(45deg, #c0392b, #e74c3c);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(231, 76, 60, 0.4);
}

@media (max-width: 768px) {
  .category {
    gap: 1.5rem;
    padding: 1rem;
  }
  
  .category .card {
    width: 100%;
    max-width: 18rem;
  }

  .filter-section {
    margin-bottom: 1.5rem;
  }
}

.banner-section {
  position: relative;
  height: 300px;
  background: linear-gradient(135deg, #9c9e36 0%, #3498db 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  margin-bottom: 2rem;
}

.banner-content {
  max-width: 800px;
  padding: 1.5rem;
}

.banner-content h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.banner-content p {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.banner-content .btn {
  padding: 0.8rem 1.5rem;
  font-size: 1.1rem;
  border-radius: 30px;
  background: linear-gradient(135deg, #2c3e50 0%, #1a252f 100%);
  border: none;
  transition: all 0.3s ease;
}

@media (max-width: 768px) {
  .banner-section {
    height: 250px;
  }

  .banner-content h1 {
    font-size: 2rem;
  }

  .banner-content p {
    font-size: 1rem;
  }
} 