/* // Navbar css */
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-boxshadow: 0 8px 6px gray;
  border-bottom: 0px solid gray !important;
}

.nav-link {
  font-weight: 300;
}

.active {
  border-bottom: 2px solid black;
}

.navbar-brand {
  font-family: 'roboto', sans-serif;
  font-weight: 700;
  letter-spacing: 3px;
}


/* Footer */
.footer {
  color: white;
  padding: 25px;
  background: #000000;
  background: -webkit-linear-gradient(
    to right,
    #434343,
    #000000);
  background: linear-gradient(to right,#434343,#000000);
}

.footer a{
  text-decoration: none;
  color: white;
  padding: 10px; 
}

.footer a:hover {
  color: #98bff1;
  border-bottom: 1px solid #98bff1;
}

/* Page Not Found */
.pnf{
  display:flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title{
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading{
  font-weight: normal;
}

.pnf-btn{
  color: black;
  border: 3px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}

/* Contact */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/*Products */

.product-link{
  text-decoration: none !important;
  color: black !important;
}